form {
  margin-bottom: 3.5rem;
}

form h2 {
  margin-top: 3.4rem;
  margin-bottom: 2.3rem;
}

.form-group {
  margin-bottom: 1.4rem;
}

.form-group .form-control,
.form-group .select-custom {
  margin-bottom: 0;
}

.form-group .form-control + .form-control {
  margin-top: 1rem;
}

.form-group-sm {
  margin-bottom: 0.9rem;
  max-width: 480px;
}

.form-control {
  height: 4.8rem;
  padding: 1.25rem 1.5rem;
  font-size: 1.4rem;
  line-height: 1.5;
  font-weight: 400;
  font-family: "Open Sans", sans-serif;
  color: #7a7d82;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 0;
  margin-bottom: 1rem;
  transition: all 0.3s;
  max-width: 480px;
}

.form-control-sm {
  height: 4.5rem;
  padding: 1.2rem 1.5rem;
  font-size: 1.3rem;
}

select.form-control:not([size]):not([multiple]) {
  height: 4.8rem;
}

select.form-control-sm:not([size]):not([multiple]) {
  height: 4.5rem;
}

.form-control:focus {
  color: #7a7d82;
  background-color: #fff;
  border-color: #282d3b;
  box-shadow: none;
}

.main .form-control::-webkit-input-placeholder {
  color: #7a7d82;
}

.main .form-control::-moz-placeholder {
  color: #7a7d82;
}

.main .form-control:-ms-input-placeholder {
  color: #7a7d82;
}

.main .form-control::-ms-input-placeholder {
  color: #7a7d82;
}

.main .form-control::placeholder {
  color: #7a7d82;
}

textarea.form-control {
  min-height: 176px;
  max-width: 680px;
}

.form-footer {
  display: flex;
  align-items: center;
  vertical-align: middle;
  margin-top: 2rem;
  margin-bottom: 3.5rem;
  font-size: 1.4rem;
}

.form-footer .form-footer-right {
  margin-left: auto;
  margin-right: 0;
}

.form-footer .form-footer-right .btn {
  margin: 0;
}

.forget-pass {
  line-height: 1.3;
}

.form-footer .btn {
  margin-right: 3rem;
}

label {
  color: #6e7075;
  font-weight: 400;
  font-size: 1.4rem;
  margin: 0 0 0.6rem;
}

.required-field > label::after {
  content: "*";
  color: #e02b27;
  font-size: 1.2rem;
  margin: 0 0 0 0.45rem;
}

.required {
  color: #e02b27;
  font-size: 1.2rem;
}

.required + .form-footer {
  margin-top: 0.8rem;
}

.select-custom {
  position: relative;
  margin-bottom: 1rem;
}

.select-custom select {
  -webkit-appearance: none;
  appearance: none;
}

.select-custom::after {
  font-family: "porto";
  content: "\e81c";
  position: absolute;
  display: inline-block;
  right: 1.9rem;
  top: 50%;
  font-size: 1.5rem;
  transform: translateY(-51%);
  color: #34373f;
}

.select-custom .form-control,
.select-custom .form-control-sm {
  padding-right: 4rem;
}

.form-group-custom-control {
  margin-top: 2rem;
}

.form-group-custom-control .custom-control {
  margin-top: 0;
  margin-bottom: 2rem;
}

.form-group-custom-control + .form-group-custom-control {
  margin-top: -0.5rem;
}

.form-group-custom-control .custom-control-label {
  text-transform: initial;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0;
  font-weight: 400;
}

.custom-control {
  position: relative;
  padding-left: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.custom-control-label {
  position: static;
  margin-bottom: 0;
  margin-top: 0.1rem;
  font-size: 1.4rem;
}

.custom-control-label:before {
  left: 0;
  top: 0.4rem;
  width: 2rem;
  height: 2rem;
  background-color: #fff;
  border: 1px solid #e6e6e6;
}

.custom-control-label:after {
  left: 0;
  top: 0.4rem;
  width: 2rem;
  height: 2rem;
}

.custom-checkbox .custom-control-label::before {
  border-radius: 0;
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  color: #282d3b;
  border-color: #08c;
  background-color: #08c;
}

.custom-checkbox
  .custom-control-input:indeterminate
  ~ .custom-control-label::before {
  background-color: #08c;
}

.custom-checkbox
  .custom-control-input:disabled:checked
  ~ .custom-control-label::before {
  background-color: rgba(0, 136, 204, 0.5);
}

.custom-checkbox
  .custom-control-input:disabled:indeterminate
  ~ .custom-control-label::before {
  background-color: rgba(0, 136, 204, 0.5);
}

.form-input {
  padding: 1rem 1.2rem;
  border-radius: 2px;
  border: 1px solid #ddd;
}

.form-wide {
  width: 100%;
}

@media screen and (min-width: 768px) {
  form,
  .form-footer {
    margin-bottom: 4rem;
  }
  form h2 {
    margin-top: 4.4rem;
  }
}

@media screen and (min-width: 992px) {
  form,
  .form-footer {
    margin-bottom: 5rem;
  }
}

