.checkout-progress-bar li > span::after {
  transition: background 0.3s;
  border-radius: 50%;
  content: "";
  left: 50%;
  position: absolute;
  top: 0;
}

.checkout-progress-bar li > span::before {
  background-color: #e4e4e4;
  height: 3.8rem;
  width: 3.8rem;
  margin-left: -1.9rem;
